import { fetchExpiryRequest } from '../../../api';
import { SET_PAY_BY_LINK_EXPIRY_DATA } from './types';

export async function fetchExpiryAction(dispatch, expiryData) {
  try {
    const response = await fetchExpiryRequest(dispatch, expiryData);
    dispatch({ type: SET_PAY_BY_LINK_EXPIRY_DATA, payload: response });
  } catch (error) {
    console.error(error);
  }
  return {};
}
