// eslint-disable-next-line no-unused-vars
import React, { useEffect, useRef } from 'react';
import { string } from 'prop-types';
import usePaymentCartContext from '../../../hooks/usePaymentCartContext';
import usePaymentAppContext from '../../../hooks/usePaymentAppContext';

function KlarnaPosPaymentPoller({ methodCode, pollerUrl }) {
  const { initKlarnaData, pollKlarna } = usePaymentAppContext();
  const { cartId } = usePaymentCartContext();

  const pollingRef = useRef(null);
  useEffect(() => {
    const pollingFunc = async () => {
      await pollKlarna({
        pollUrl: pollerUrl,
        sessionId: initKlarnaData.session_id,
        sessionUrl: initKlarnaData.session_url,
        quoteId: cartId,
        methodCode,
      });
    };

    const startPolling = () => {
      pollingRef.current = setInterval(() => {
        pollingFunc();
      }, 5000); // Poll every 5 seconds
    };
    startPolling();

    return () => {
      clearInterval(pollingRef.current);
    };
  }, [cartId, initKlarnaData, methodCode, pollKlarna, pollerUrl]);

  return null;
}

KlarnaPosPaymentPoller.propTypes = {
  methodCode: string.isRequired,
  pollerUrl: string.isRequired,
};

export default KlarnaPosPaymentPoller;
