import React from 'react';
import { func, object, shape } from 'prop-types';
import RadioInput from '../../../../components/common/Form/RadioInput';
import AdyenPayByLinkSelected from './AdyenPayByLinkSelected';

function AdyenPayByLinkContent({ method, selected, actions }) {
  return (
    <div className="border-t border-b py-4">
      <RadioInput
        value={method.code}
        label={method.title}
        name="adyen_pay_by_link"
        // disabled={!paymentAvailable}
        onChange={actions.change}
        checked={method.code === selected.code}
      />
      {method.code === selected.code && (
        <AdyenPayByLinkSelected methodCode={method.code} />
      )}
    </div>
  );
}

AdyenPayByLinkContent.propTypes = {
  method: object,
  selected: object,
  actions: shape({
    change: func,
  }),
};

AdyenPayByLinkContent.defaultProps = {
  method: {},
  selected: {},
  actions: {
    change: () => {},
  },
};

export default AdyenPayByLinkContent;
