/* eslint-disable no-console */
import React, { useMemo } from 'react';
import { node } from 'prop-types';

import PaymentConfigContext from './PaymentConfigContext';

function PaymentConfigProvider({ children }) {
  const checkoutConfig = document
    .getElementById('react-checkout')
    .getAttribute('data-checkout_config'); // loaded by hyva viewmodel

  const context = useMemo(() => [checkoutConfig], [checkoutConfig]);

  return (
    <PaymentConfigContext.Provider value={context}>
      {children}
    </PaymentConfigContext.Provider>
  );
}

PaymentConfigProvider.propTypes = {
  children: node.isRequired,
};

export default PaymentConfigProvider;
