/* eslint-disable no-console */
import { get as _get } from 'lodash-es';
import sendRequest, { RESPONSE_JSON } from '../../sendRequest';

export default async function disableKlarna(dispatch, klarnaDisableDetails) {
  const url = _get(klarnaDisableDetails, 'disableUrl');
  const data = {
    sessionId: _get(klarnaDisableDetails, 'sessionId'),
    sessionUrl: _get(klarnaDisableDetails, 'sessionUrl'),
    quoteId: _get(klarnaDisableDetails, 'quoteId'),
    masked: true,
  };
  console.log(data);
  return sendRequest(dispatch, data, url, RESPONSE_JSON, {}, true);
}
