/* eslint-disable no-unused-vars */
/* eslint-disable no-console */
import React from 'react';
import { func, object, shape } from 'prop-types';
import NvmAdyenContent from './NvmAdyenContent';

function NvmAdyen({ method, selected, actions }) {
  return (
    <NvmAdyenContent method={method} selected={selected} actions={actions} />
  );
}

NvmAdyen.propTypes = {
  method: object,
  selected: object,
  actions: shape({
    change: func,
  }),
};

NvmAdyen.defaultProps = {
  method: {},
  selected: {},
  actions: {
    change: () => {},
  },
};

export default NvmAdyen;
