/* eslint-disable no-console */
import React from 'react';
import { func, object, shape } from 'prop-types';
import RadioInput from '../../../../components/common/Form/RadioInput';
// import KlarnaPosPaymentSelected from './KlarnaPosPaymentSelected';
import NvmAdyenSelected from './NvmAdyenSelected';

function NvmAdyenContent({ method, selected, actions }) {
  return (
    <div className="border-t border-b py-4">
      <RadioInput
        value={method.code}
        label={method.title}
        name="newvoicemedia_pci_pal_adyen"
        // disabled={!paymentAvailable}
        onChange={actions.change}
        checked={method.code === selected.code}
      />
      {method.code === selected.code && (
        <NvmAdyenSelected methodCode={method.code} />
      )}
    </div>
  );
}

NvmAdyenContent.propTypes = {
  method: object,
  selected: object,
  actions: shape({
    change: func,
  }),
};

NvmAdyenContent.defaultProps = {
  method: {},
  selected: {},
  actions: {
    change: () => {},
  },
};

export default NvmAdyenContent;
