/* eslint-disable no-unused-vars */
/* eslint-disable no-console */
import React, { useEffect, useState, useCallback } from 'react';

import { get as _get } from 'lodash-es';
import { string, bool } from 'prop-types';
import usePaymentAppContext from '../../../hooks/usePaymentAppContext';
import usePaymentConfigContext from '../../../hooks/usePaymentConfigContext';
import PlaceOrderButton from '../../../PlaceOrderButton';
import IFrame from '../../../IFrame';
import KlarnaPosPaymentPoller from './KlarnaPosPaymentPoller';
import KlarnaPosPaymentInitSessionButton from './KlarnaPosPaymentInitSessionButton';

function KlarnaPosPaymentSelected({ methodCode }) {
  const { config } = usePaymentConfigContext('klarna');
  const {
    initKlarnaData,
    initKlarna,
    klarnaPollResponse,
    klarnaEnv,
    setKlarnaEnv,
  } = usePaymentAppContext();

  const [environment, setEnvironment] = useState('');
  const [shouldInitKlarna, setShouldInitKlarna] = useState(false);

  const setContextKlarnaEnv = useCallback(
    (env) => {
      setKlarnaEnv(env);
    },
    [setKlarnaEnv]
  );

  useEffect(() => {
    if (shouldInitKlarna && !initKlarnaData && config) {
      setEnvironment(config.environment === 'Sandbox' ? 'sandbox' : 'live');
      if (!klarnaEnv) {
        setContextKlarnaEnv(
          config.environment === 'Sandbox' ? 'sandbox' : 'live'
        );
      }

      if (config[`init_session_url_${environment}`]) {
        (async () => {
          await initKlarna(config[`init_session_url_${environment}`]);
        })();
      }
    }
  }, [
    config,
    initKlarna,
    methodCode,
    environment,
    initKlarnaData,
    shouldInitKlarna,
    klarnaPollResponse,
    setContextKlarnaEnv,
    klarnaEnv,
  ]);

  return (
    <div className="py-4">
      {initKlarnaData && config[`poller_url_${environment}`] && (
        <>
          <IFrame title="klarnaPosIFrame" src={initKlarnaData.standalone_url} />
          <p>Session ID: {initKlarnaData.session_id} </p>
          <KlarnaPosPaymentPoller
            methodCode={methodCode}
            pollerUrl={config[`poller_url_${environment}`]}
          />
        </>
      )}
      {klarnaPollResponse && <p>Status: {klarnaPollResponse.status}</p>}
      <div className="py-2">
        <PlaceOrderButton methodCode={methodCode} automated />
        {config && (
          <KlarnaPosPaymentInitSessionButton
            shouldInitKlarna={shouldInitKlarna}
            setShouldInitKlarna={setShouldInitKlarna}
            label={config.button_label}
          />
        )}
      </div>
    </div>
  );
}

KlarnaPosPaymentSelected.propTypes = {
  methodCode: string.isRequired,
};

export default KlarnaPosPaymentSelected;
