/* eslint-disable no-console */
import React from 'react';
import { object, func } from 'prop-types';
import { get as _get } from 'lodash-es';
import methodRenderers from './customRenderers';
import usePaymentAppContext from './hooks/usePaymentAppContext';
import usePaymentConfigContext from './hooks/usePaymentConfigContext';
import usePaymentCartContext from './hooks/usePaymentCartContext';

/*
 * inject payment selection into the main payment app in order to more easily deal with selection change regarding state
 */
function PaymentsContainer({
  methodList,
  setFieldValue,
  setFieldTouched,
  fields,
  paymentValues,
}) {
  const { resetState, initKlarnaData, disableKlarna, klarnaEnv } =
    usePaymentAppContext();
  const { config } = usePaymentConfigContext('klarna');
  const { cartId } = usePaymentCartContext();

  const handlePaymentMethodSelection = async (event) => {
    if (initKlarnaData) {
      (async () => {
        console.log({
          // disable any open klarna sessions if one is open
          disableUrl: _get(config, `disabler_url_${klarnaEnv}`),
          sessionId: _get(initKlarnaData, 'session_id'),
          sessionUrl: _get(initKlarnaData, 'session_url'),
          quoteId: cartId,
        });
        await disableKlarna({
          // disable any open klarna sessions if one is open
          disableUrl: _get(config, `disabler_url_${klarnaEnv}`),
          sessionId: _get(initKlarnaData, 'session_id'),
          sessionUrl: _get(initKlarnaData, 'session_url'),
          quoteId: cartId,
        });
      })();
    }

    resetState();

    const methodSelected = _get(methodList, `${event.target.value}.code`);

    if (!methodSelected) {
      return;
    }

    await setFieldValue(fields.code, methodSelected);
    setFieldTouched(fields.code, true);
  };

  return (
    methodList &&
    methodRenderers && (
      <ul>
        {Object.keys(methodList).map((methodName) => {
          const MethodRenderer = methodRenderers[methodName];
          if (!MethodRenderer) {
            console.error(`no method renderer for ${methodName}`);
          }
          return (
            <li key={methodList[methodName].code}>
              <MethodRenderer
                method={methodList[methodName]}
                selected={paymentValues}
                actions={{ change: handlePaymentMethodSelection }}
              />
            </li>
          );
        })}
      </ul>
    )
  );
}

PaymentsContainer.propTypes = {
  methodList: object,
  setFieldValue: func,
  setFieldTouched: func,
  fields: object,
  paymentValues: object,
};

PaymentsContainer.defaultProps = {
  methodList: {},
  setFieldValue: () => {},
  setFieldTouched: () => {},
  fields: {},
  paymentValues: {},
};

export default PaymentsContainer;
