/* eslint-disable no-console */
import {
  initKlarnaRequest,
  pollKlarnaRequest,
  disableKlarnaRequest,
} from '../../../api';
import {
  SET_CONT_INIT_KLARNA_DATA,
  SET_KLARNA_POLL_RESPONSE,
  SET_KLARNA_ENV,
} from './types';
import {
  SET_ADDITIONAL_DATA,
  SET_EXTRA_PAYMENT_DATA,
} from '../paymentData/types';

export async function initKlarnaAction(dispatch, klarnaInitUrl) {
  try {
    const response = await initKlarnaRequest(dispatch, klarnaInitUrl);
    dispatch({ type: SET_CONT_INIT_KLARNA_DATA, payload: response });
    return response;
  } catch (error) {
    console.error(error);
  }
  return {};
}

export async function pollKlarnaAction(dispatch, klarnaPollDetails) {
  try {
    const response = await pollKlarnaRequest(dispatch, klarnaPollDetails);
    dispatch({ type: SET_KLARNA_POLL_RESPONSE, payload: response });
    if (response && response.status === 'COMPLETED') {
      dispatch({
        type: SET_ADDITIONAL_DATA,
        payload: {
          method_title: klarnaPollDetails.methodCode,
          klarna_reference: response.klarna_reference,
          klarna_order_id: response.order_id,
        },
      });
      dispatch({
        type: SET_EXTRA_PAYMENT_DATA,
        payload: {},
      });
    }

    return response;
  } catch (error) {
    console.error(error);
  }
  return {};
}

export async function disableKlarnaAction(dispatch, klarnaDisableDetails) {
  try {
    const response = await disableKlarnaRequest(dispatch, klarnaDisableDetails);
    console.log(response);
    return response;
  } catch (error) {
    console.error(error);
  }
  return {};
}

export async function setKlarnaEnvAction(dispatch, klarnaEnv) {
  try {
    dispatch({ type: SET_KLARNA_ENV, payload: klarnaEnv });
  } catch (error) {
    console.error(error);
  }
}
