/* eslint-disable camelcase */
/* eslint-disable no-unused-vars */
/* eslint-disable no-console */
import React, { useEffect, useState } from 'react';

import { get as _get } from 'lodash-es';
import { string } from 'prop-types';
import usePaymentCartContext from '../../../hooks/usePaymentCartContext';
import usePaymentAppContext from '../../../hooks/usePaymentAppContext';
import PlaceOrderButton from '../../../PlaceOrderButton';

function AdyenPayByLinkSelected({ methodCode }) {
  const { shipping, address } = usePaymentCartContext();
  const {
    payByLinkExpiryData,
    fetchExpiry,
    setAdditionalData,
    setExtraPaymentData,
  } = usePaymentAppContext();

  useEffect(() => {
    if (shipping && address && !payByLinkExpiryData) {
      (async () => {
        try {
          const carrier_code = _get(shipping, 'carrierCode');
          const method_code = _get(shipping, 'methodCode');
          const postcode = _get(address, 'zipcode');
          await fetchExpiry({
            carrier_code,
            method_code,
            postcode,
          });
          setExtraPaymentData({});
          setAdditionalData({
            method_title: methodCode,
            pay_by_link: '',
          });
        } catch (error) {
          console.error(error);
        }
      })();
    }
  });

  return (
    payByLinkExpiryData && (
      <div className="py-4">
        <p>
          This payment will be valid until:{' '}
          {_get(payByLinkExpiryData, 'expiry_date')}{' '}
        </p>
        <p>
          The customer will be notified of the expiry date for the payment link
          in the confirmation email.
        </p>
        <div className="py-2">
          <PlaceOrderButton methodCode={methodCode} />
        </div>
      </div>
    )
  );
}

AdyenPayByLinkSelected.propTypes = {
  methodCode: string.isRequired,
};

export default AdyenPayByLinkSelected;
