import React from 'react';
import { string } from 'prop-types';

function IFrame({ title, src }) {
  return (
    <div className="w-full">
      <iframe title={title} src={src} width="100%" height="750" />
    </div>
  );
}

IFrame.propTypes = {
  title: string,
  src: string,
};

IFrame.defaultProps = {
  title: '',
  src: '',
};

export default IFrame;
