import React, { useEffect, useCallback } from 'react';
import { string, bool } from 'prop-types';
import Button from '../components/common/Button';
import usePerformPlaceOrderByREST from './hooks/usePerformPlaceOrderByREST';
import usePaymentAppContext from './hooks/usePaymentAppContext';

function PlaceOrderButton({ methodCode, automated }) {
  const placeOrderCall = usePerformPlaceOrderByREST(methodCode);
  const { extraPaymentData, additionalData } = usePaymentAppContext();

  const handlePlaceOrder = useCallback(async () => {
    if (extraPaymentData && additionalData) {
      await placeOrderCall(extraPaymentData, additionalData);
    }
  }, [additionalData, extraPaymentData, placeOrderCall]);

  useEffect(() => {
    if (extraPaymentData && additionalData && automated) {
      handlePlaceOrder();
    }
  }, [extraPaymentData, additionalData, automated, handlePlaceOrder]);

  return (
    !automated && (
      <Button
        variant="primary"
        size="lg"
        click={handlePlaceOrder}
        disable={!extraPaymentData || !additionalData}
      >
        Place Order
      </Button>
    )
  );
}

PlaceOrderButton.propTypes = {
  methodCode: string,
  automated: bool,
};

PlaceOrderButton.defaultProps = {
  methodCode: '',
  automated: false,
};

export default PlaceOrderButton;
