import { SET_PAY_BY_LINK_EXPIRY_DATA } from './adyenPayByLink/types';
import { setPayByLinkExpiryData } from './adyenPayByLink/reducers';
import {
  SET_CONT_INIT_KLARNA_DATA,
  SET_KLARNA_POLL_RESPONSE,
  SET_KLARNA_ENV,
} from './klarnaPosPayment/types';
import {
  setContInitKlarnaData,
  setKlarnaPollResponse,
  setKlarnaEnv,
} from './klarnaPosPayment/reducers';
import {
  SET_ADDITIONAL_DATA,
  SET_EXTRA_PAYMENT_DATA,
} from './paymentData/types';
import { setAdditionalData, setExtraPaymentData } from './paymentData/reducers';
import { RESET_STATE } from './state/types';
import { resetState } from './state/reducers';
import { SET_INIT_NVM_DATA } from './nvmAdyen/types';
import { setInitNvmData } from './nvmAdyen/reducers';

const actions = {
  [SET_CONT_INIT_KLARNA_DATA]: setContInitKlarnaData,
  [SET_KLARNA_POLL_RESPONSE]: setKlarnaPollResponse,
  [SET_KLARNA_ENV]: setKlarnaEnv,
  [SET_ADDITIONAL_DATA]: setAdditionalData,
  [SET_EXTRA_PAYMENT_DATA]: setExtraPaymentData,
  [SET_PAY_BY_LINK_EXPIRY_DATA]: setPayByLinkExpiryData,
  [RESET_STATE]: resetState,
  [SET_INIT_NVM_DATA]: setInitNvmData,
};

export default function PaymentAppReducer(state, { type, payload }) {
  const action = actions[type];
  if (action) {
    return action(state, payload);
  }
  return state;
}
