import { get as _get } from 'lodash-es';
import useCartContext from '../../hook/useCartContext';

export default function usePaymentCartContext() {
  const { cart } = useCartContext();
  const shipping = _get(cart, 'selected_shipping_method');
  const address = _get(cart, 'shipping_address');
  const cartId = _get(cart, 'id');

  return {
    shipping,
    address,
    cartId,
  };
}
