import React from 'react';
import { bool, func } from 'prop-types';
import Button from '../../../../components/common/Button';

function KlarnaPosPaymentInitSessionButton({
  shouldInitKlarna,
  setShouldInitKlarna,
  label,
}) {
  const handleClick = () => {
    setShouldInitKlarna(true);
  };

  return (
    !shouldInitKlarna && (
      <Button
        variant="primary"
        size="lg"
        click={handleClick}
        disable={shouldInitKlarna}
      >
        {label}
      </Button>
    )
  );
}

KlarnaPosPaymentInitSessionButton.propTypes = {
  shouldInitKlarna: bool.isRequired,
  setShouldInitKlarna: func.isRequired,
  label: String.isRequired,
};

export default KlarnaPosPaymentInitSessionButton;
