import { SET_ADDITIONAL_DATA, SET_EXTRA_PAYMENT_DATA } from './types';

export async function setAdditionalDataAction(dispatch, additionalData) {
  try {
    dispatch({ type: SET_ADDITIONAL_DATA, payload: additionalData });
  } catch (error) {
    console.error(error);
  }
  return {};
}

export async function setExtraPaymentDataAction(dispatch, extraPaymentData) {
  try {
    dispatch({ type: SET_EXTRA_PAYMENT_DATA, payload: extraPaymentData });
  } catch (error) {
    console.error(error);
  }
  return {};
}
