import sendRequest, { RESPONSE_JSON } from '../../sendRequest';

export default async function fetchExpiry(dispatch, expiryData) {
  const relativeUrl = '/vaxltd_adyen_pay_by_link/checkout/delivery';
  return sendRequest(
    dispatch,
    expiryData,
    relativeUrl,
    RESPONSE_JSON,
    {},
    true
  );
}
