/* eslint-disable no-console */
/* eslint-disable no-unused-vars */
import { get as _get } from 'lodash-es';
import { useContext } from 'react';

import PaymentConfigContext from '../context/PaymentConfig/PaymentConfigContext';

export default function usePaymentConfigContext(paymentMethod) {
  const [checkoutConfig] = useContext(PaymentConfigContext);

  const data = JSON.parse(checkoutConfig);
  const config = _get(data, `payment.${paymentMethod}`);

  return {
    config,
  };
}
