/* eslint-disable no-console */
import { React } from 'react';
import { func, object, shape } from 'prop-types';
import RadioInput from '../../../../components/common/Form/RadioInput';
import KlarnaPosPaymentSelected from './KlarnaPosPaymentSelected';

function KlarnaPosPaymentContent({ method, selected, actions }) {
  return (
    <div className="border-t border-b py-4">
      <RadioInput
        value={method.code}
        label={method.title}
        name="klarnapospayment"
        // disabled={!paymentAvailable}
        onChange={actions.change}
        checked={method.code === selected.code}
      />
      {method.code === selected.code && (
        <KlarnaPosPaymentSelected methodCode={method.code} />
      )}
    </div>
  );
}

KlarnaPosPaymentContent.propTypes = {
  method: object,
  selected: object,
  actions: shape({
    change: func,
  }),
};

KlarnaPosPaymentContent.defaultProps = {
  method: {},
  selected: {},
  actions: {
    change: () => {},
  },
};

export default KlarnaPosPaymentContent;
