import React from 'react';
import { bool, func } from 'prop-types';
import Button from '../../../../components/common/Button';

function NvmAdyenInitSessionButton({ shouldInitNvm, setShouldInitNvm, label }) {
  const handleClick = () => {
    setShouldInitNvm(true);
  };

  return (
    !shouldInitNvm && (
      <Button
        variant="primary"
        size="lg"
        click={handleClick}
        disable={shouldInitNvm}
      >
        {label}
      </Button>
    )
  );
}

NvmAdyenInitSessionButton.propTypes = {
  shouldInitNvm: bool.isRequired,
  setShouldInitNvm: func.isRequired,
  label: String.isRequired,
};

export default NvmAdyenInitSessionButton;
