export function setContInitKlarnaData(state, initKlarnaData) {
  return {
    ...state,
    initKlarnaData,
  };
}

export function setKlarnaPollResponse(state, response) {
  return {
    ...state,
    klarnaPollResponse: response,
  };
}

export function setKlarnaEnv(state, klarnaEnv) {
  return {
    ...state,
    klarnaEnv,
  };
}
