/* eslint-disable no-console */
/* eslint-disable no-unused-vars */
import React, { useState, useEffect } from 'react';
import { string } from 'prop-types';
import usePaymentConfigContext from '../../../hooks/usePaymentConfigContext';
import usePaymentAppContext from '../../../hooks/usePaymentAppContext';
import usePaymentCartContext from '../../../hooks/usePaymentCartContext';
import useAppContext from '../../../../hook/useAppContext';
import NvmAdyenInitSessionButton from './NvmAdyenInitSessionButton';

function NvmAdyenSelected({ methodCode }) {
  const { config } = usePaymentConfigContext('nvmAdyen');
  const { initNvm, initNvmData } = usePaymentAppContext();
  const [shouldInitNvm, setShouldInitNvm] = useState(false);
  const { setErrorMessage } = useAppContext();
  const { cartId } = usePaymentCartContext();

  const createSessionUri = 'nvm_pcipal_adyen/session/create';

  useEffect(() => {
    if (shouldInitNvm && !initNvmData) {
      (async () => {
        const response = await initNvm({
          url: createSessionUri,
          quoteId: cartId,
        });
        if (!response.success) {
          setErrorMessage(response.message);
        }
      })();
    }
  }, [cartId, config, initNvm, initNvmData, setErrorMessage, shouldInitNvm]);

  return (
    <div className="py-2">
      {config && (
        <NvmAdyenInitSessionButton
          shouldInitNvm={shouldInitNvm}
          setShouldInitNvm={setShouldInitNvm}
          label="Continue with NVM Adyen"
        />
      )}
    </div>
  );
}

NvmAdyenSelected.propTypes = {
  methodCode: string.isRequired,
};

export default NvmAdyenSelected;
