/* eslint-disable no-console */
import { fetchExpiryAction } from './adyenPayByLink/actions';
import {
  disableKlarnaAction,
  initKlarnaAction,
  pollKlarnaAction,
  setKlarnaEnvAction,
} from './klarnaPosPayment/actions';
import {
  setAdditionalDataAction,
  setExtraPaymentDataAction,
} from './paymentData/actions';
import { initNvmAction } from './nvmAdyen/actions';
import { resetStateAction } from './state/actions';

const dispatchMapper = {
  fetchExpiry: fetchExpiryAction,
  initKlarna: initKlarnaAction,
  pollKlarna: pollKlarnaAction,
  disableKlarna: disableKlarnaAction,
  setKlarnaEnv: setKlarnaEnvAction,
  setAdditionalData: setAdditionalDataAction,
  setExtraPaymentData: setExtraPaymentDataAction,
  resetState: resetStateAction,
  initNvm: initNvmAction,
};

export default function PaymentAppDispatcher(dispatch) {
  const dispatchers = { dispatch };
  Object.keys(dispatchMapper).forEach((dispatchName) => {
    dispatchers[dispatchName] = dispatchMapper[dispatchName].bind(
      null,
      dispatch
    );
  });

  return dispatchers;
}
