import React, { useMemo, useReducer } from 'react';
import { node } from 'prop-types';
import PaymentAppReducer from './PaymentAppReducer';
import PaymentAppContext from './PaymentAppContext';
import PaymentAppDispatchers from './PaymentAppDispatcher';
import initialState from './state/initialState';

function AppDataProvider({ children }) {
  const [paymentAppData, dispatch] = useReducer(
    PaymentAppReducer,
    initialState
  );

  const paymentAppActions = useMemo(
    () => PaymentAppDispatchers(dispatch),
    [dispatch]
  );

  const context = useMemo(
    () => [paymentAppData, paymentAppActions],
    [paymentAppData, paymentAppActions]
  );

  return (
    <PaymentAppContext.Provider value={context}>
      {children}
    </PaymentAppContext.Provider>
  );
}

AppDataProvider.propTypes = {
  children: node.isRequired,
};

export default AppDataProvider;
