/* eslint-disable no-console */
import { initNvmRequest } from '../../../api';

import { SET_INIT_NVM_DATA } from './types';

export async function initNvmAction(dispatch, nvmInitdata) {
  try {
    const response = await initNvmRequest(dispatch, nvmInitdata);
    dispatch({ type: SET_INIT_NVM_DATA, payload: response });
    return response;
  } catch (error) {
    console.error(error);
  }
  return {};
}
