/* eslint-disable no-console */
import React from 'react';
import { func, object, shape } from 'prop-types';
import KlarnaPosPaymentContent from './KlarnaPosPaymentContent';

function KlarnaPosPayment({ method, selected, actions }) {
  return (
    <KlarnaPosPaymentContent
      method={method}
      selected={selected}
      actions={actions}
    />
  );
}

KlarnaPosPayment.propTypes = {
  method: object,
  selected: object,
  actions: shape({
    change: func,
  }),
};

KlarnaPosPayment.defaultProps = {
  method: {},
  selected: {},
  actions: {
    change: () => {},
  },
};

export default KlarnaPosPayment;
