import { get as _get } from 'lodash-es';
import sendRequest, { RESPONSE_JSON } from '../../sendRequest';

export default async function pollKlarna(dispatch, klarnaPollDetails) {
  const url = _get(klarnaPollDetails, 'pollUrl');
  const data = {
    sessionId: _get(klarnaPollDetails, 'sessionId'),
    sessionUrl: _get(klarnaPollDetails, 'sessionUrl'),
    quoteId: _get(klarnaPollDetails, 'quoteId'),
    masked: true,
  };
  return sendRequest(dispatch, data, url, RESPONSE_JSON, {}, true);
}
